body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  /*font-size: 16px;*/
  line-height: 1.5;
  color: $color-black;
}

// FONT
$font-size-base: 1rem; //16px
$font-size-large: 1.25rem; //20px
$font-size-xlarge: 1.5rem; //24px
$font-size-h6: 1.25rem; //20px
$font-size-h5: 1.563em; //25px
$font-size-h4: 1.953em; //31.25px
$font-size-h3: 2.441em; //39.0625px
$font-size-h2: 3.052em; //48.8281..px
$font-size-h1: 3.815em; //61.0351..px

strong {
  font-size: 1.25em;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 900;
  margin: 0;
}
h {
  &1 {
    font-size: $font-size-h1;
  }
  &2 {
    font-size: $font-size-h2;
  }
  &3 {
    font-size: $font-size-h3;
  }
  &4 {
    font-size: $font-size-h4;
  }
  &5 {
    font-size: $font-size-h5;
  }
  &6 {
    font-size: $font-size-h6;
  }
}

.text {
  &-large {
    font-size: $font-size-large;
  }
  &-bold {
    font-weight: 700;
  }
}

.btn {
  font-size: 1.25em;
}

@media only screen and (max-width: $bp-smallfont) {
  // html font: 14px
  $font-size-base: 1rem; //14px
  $font-size-large: 1.286rem; //18px
  $font-size-xlarge: 1.571rem; //22px
  $font-size-h6: 1rem; //14px
  $font-size-h5: 1.25rem; //17.5px
  $font-size-h4: 1.563em; //21.875
  $font-size-h3: 1.953em; //27.34375px
  $font-size-h2: 2.441em; //34.1796..px
  $font-size-h1: 3.052em; //42.7246..px

  h {
    &1 {
      font-size: $font-size-h1;
    }
    &2 {
      font-size: $font-size-h2;
    }
    &3 {
      font-size: $font-size-h3;
    }
    &4 {
      font-size: $font-size-h4;
    }
    &5 {
      font-size: $font-size-h5;
    }
    &6 {
      font-size: $font-size-h6;
    }
  }

  .text {
    &-large {
      font-size: $font-size-large;
    }
    &-bold {
      font-weight: 700;
    }
  }
}
