.grid12 {
  display: grid;
  grid-template-columns: [center-start] repeat(12, [col-start] minmax(0px, 73px) [col-end]) [center-end];
  column-gap: 24px;
  align-items: center;
}

.grid12-5 {
  grid-column: span 5;
}

.grid12-6 {
  grid-column: span 6;
}

.grid12-7 {
  grid-column: span 7;
}

.flex {
  display: flex;
}
.flex-col {
  flex-direction: column;
}

.flexible {
  flex-grow: 1;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }
  80% {
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }
  80% {
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
