.header .logo {
  width: fit-content;
  margin-right: 80px;
}
.header .logo img {
  height: 50px;
  width: auto;
}
.header .nav .main-menu > li {
  margin-right: 2.5rem;
  line-height: 50px;
  display: inline-block;
  border-bottom: unset;
}
.header .nav .main-menu > li > a > span:first-child {
  display: inline-block;
  position: relative;
}
.header .nav .main-menu > li > a > span:first-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #009fe3;
  height: 4px;
  border-radius: 10px;
}
.header .nav .sub-menu:first-child {
  margin-top: 1rem;
  left: 0px;
}
.header .nav ul {
  padding: 0;
  margin: 0;
  position: relative;
}
.header .nav ul li {
  display: inline-block;
  padding: 1rem 0px;
  margin: 0px 1.5em;
  /* Display Dropdowns on Hover */
}
.header .nav ul li a {
  white-space: nowrap;
}
.header .nav ul li:hover > ul {
  transition-delay: 0s;
  visibility: inherit;
}
.header .nav ul ul {
  /* Hide Dropdowns by Default */
  transition: all 0s ease 0.3s; /*delay 1s*/
  visibility: hidden;
  position: absolute;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  background-color: white;
  font-size: 18px;
  /* Fisrt Tier Dropdown */
  /* Second, Third and more Tiers	*/
}
.header .nav ul ul li {
  float: none;
  display: list-item;
  position: relative;
  line-height: 1.5em;
}
.header .nav ul ul li:not(:last-child) {
  border-bottom: 1px solid #e3e3e3;
}
.header .nav ul ul ul {
  top: 0px;
  right: -2em;
  translate: 100%;
}
.header .nav ul ul ul li {
  position: relative;
  right: 0;
}
.header .header-meta {
  column-gap: 3.5rem;
  margin-left: auto;
}

